  .animated-btn {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    line-height: 80px;
    display: inline-block;
    text-align: center;
    background: #ff3f3f;
    position: relative;
}

.animated-btn::before, .animated-btn::after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border-radius: 50%;
    background: rgba(255, 63, 63, 0.8);
    -webkit-animation: ripple-1 2s infinite ease-in-out;
            animation: ripple-1 2s infinite ease-in-out;
    z-index: -1;
}

.animated-btn::after {
    background: rgba(255, 63, 63, 0.6);
    -webkit-animation: ripple-2 2s infinite ease-in-out;
            animation: ripple-2 2s infinite ease-in-out;
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
}

@-webkit-keyframes ripple-1 {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
        opacity: 0;
    }
}

@keyframes ripple-1 {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
        opacity: 0;
    }
}

@-webkit-keyframes ripple-2 {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(1.7);
                transform: scale(1.7);
        opacity: 0;
    }
}

@keyframes ripple-2 {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(1.7);
                transform: scale(1.7);
        opacity: 0;
    }
}

